<template>
    <a-spin :spinning="loading">
        <a-list
                itemLayout="horizontal"
                :dataSource="data"
        >
            <a-list-item slot="renderItem" slot-scope="item, index">
                <a-list-item-meta
                        :description="item.comment_content"
                >
                    <a slot="title">{{item.user_name}}</a>
                    <a-avatar slot="avatar" :src="getUrl(item.user_thumbnail_format)"/>
                </a-list-item-meta>
            </a-list-item>
        </a-list>
        <a-pagination
                style="float: right"
                v-model="current"
                :total="total"
                :pageSize="pageSize"
                @change="change"/>
    </a-spin>
</template>

<script>
    import {getDate, getImageUrl} from '@/libs/tools'

    export default {
        props: {
            columns: {
                required: true,
                type: Array,
                validator: value => {
                    if (value.length === 0) {
                        throw 'custom validator check failed for prop "columns" length error';
                    }

                    for (let i = 0; i < value.length; i++) {
                        if (value[i].hasOwnProperty('scopedSlots')
                            && value[i].scopedSlots.customRender === 'operation') {
                            if (!value[i].hasOwnProperty('buttons')) {
                                throw 'custom validator check failed for prop "columns" property buttons missing error';
                            }

                            if (value[i].buttons instanceof Object !== true) {
                                throw 'custom validator check failed for prop "columns" property buttons type error';
                            }
                        }
                    }
                    return true;
                }
            },
            request: {
                required: true,
                type: Function,
            },
            requestInitFilter: {
                required: false,
                type: [Object],
                default: () => {
                    return {}
                }
            }
        },
        data() {
            return {
                loading: false,
                data: [],
                pagination: {},
                filter: {},//外部传入参数缓存
            }
        },
        computed: {
            operationButtons() {
                let value = this.columns;
                for (let i = 0; i < value.length; i++) {
                    if (value[i].hasOwnProperty('scopedSlots')
                        && value[i].scopedSlots.customRender === 'operation') {
                        return value[i].buttons;
                    }
                }
                return {};
            },
            operationButtonsLength() {
                let value = this.columns;
                let buttons;
                for (let i = 0; i < value.length; i++) {
                    if (value[i].hasOwnProperty('scopedSlots')
                        && value[i].scopedSlots.customRender === 'operation') {
                        buttons = value[i].buttons;
                        break;
                    }
                }

                let count = 0;
                for (let key in buttons) {
                    count++;
                }
                return count;
            },
            current() {
                return this.pagination.hasOwnProperty('current') ? this.pagination.current : 0;
            },
            total() {
                return this.pagination.hasOwnProperty('total') ? this.pagination.total : 0;
            },
            pageSize() {
                return this.pagination.hasOwnProperty('pageSize') ? this.pagination.pageSize : 0;
            },
        },
        methods: {
            getUrl(url) {
                return getImageUrl(url);
            },
            formatTimestamp(t) {
                return getDate(t);
            },
            change(current) {
                this.fetch({
                    current_page: current,
                    filter: this.filter
                });
            },
            handleSearch(filter) {
                this.filter = filter;
                this.fetch({current_page: 1, filter});
            },
            fetch({current_page, filter}) {
                this.loading = true;
                if (!filter) {
                    this.filter = {};
                }
                return this.request({current_page, filter})
                    .then((res) => {
                        this.data = res.data.rows;
                        this.pagination = res.data.pagination;
                        this.loading = false;
                        return res;
                    })
                    .catch(res => {
                        this.loading = false;
                    });
            },
            reFetch() {
               return this.fetch({current_page: this.pagination.current, filter: this.filter})
            },
            handleOperate(type, row) {
                let operate = this.$emit('handleOperate', type, row);
            },
            isButtonVisible(type, row) {
                switch (typeof this.operationButtons[type].visible) {
                    case "boolean":
                        return this.operationButtons[type].visible;
                    case "function":
                        return this.operationButtons[type].visible(row);
                    case "undefined":
                        return true;
                }
            },
            isButtonDisabled(type, row) {
                switch (typeof this.operationButtons[type].disable) {
                    case "boolean":
                        return this.operationButtons[type].disable;
                    case "function":
                        return this.operationButtons[type].disable(row);
                    case "undefined":
                        return false;
                }
            }
        },
        created() {
            this.filter = this.requestInitFilter;
            this.fetch({current_page: 1, filter: this.filter});
        },
    }
</script>
