<template>
    <a-table
            :size="size"
            :loading="loading"
            :showHeader="showHeader"
            rowKey="id"
            :columns="columns_computed"
            :dataSource="data"
            :pagination="pagination"
            :rowSelection="rowSelection"
            @change="handleTableChange">
        <template slot="index" slot-scope="text, record, index">
            <div>{{getIndexNum(index)}}</div>
        </template>
        <template slot="date_time" slot-scope="text, record, index">
            {{formatTimestamp(text)}}
        </template>
        <template slot="audit_status" slot-scope="text, record, index">
            <template v-if="text==='通过'">
                <a-tag color="#87d068">{{text}}</a-tag>
            </template>
            <template v-else-if="text==='未通过'||text==='不通过'">
                <a-tooltip placement="top" :title="getAuditReason(record)">
                    <a-tag color="#f50">{{text}}</a-tag>
                </a-tooltip>
            </template>
            <template v-else>
                <a-tag color="#d9d9d9">{{text}}</a-tag>
            </template>
        </template>
        <template slot="operation" slot-scope="text, record, index">
            <div class='editable-row-operations'>
                <template v-if="operationButtons.hasOwnProperty('check')" v-show="isButtonVisible('check',record)">
                    <a-button type="primary"
                              size="small"
                              :disabled="isButtonDisabled('check',record)"
                              @click="handleOperate('check',record)">
                        {{operationButtons.check.hasOwnProperty('title')?operationButtons.check.title:'查看'}}
                    </a-button>
                    <a-divider type="vertical" v-if="operationButtonsLength>1"/>
                </template>
                <template v-if="operationButtons.hasOwnProperty('create')" v-show="isButtonVisible('create',record)">
                    <a-button type="primary"
                              size="small"
                              :disabled="isButtonDisabled('create',record)"
                              @click="handleOperate('create',record)">
                        {{operationButtons.create.hasOwnProperty('title')?operationButtons.create.title:'新增'}}
                    </a-button>
                    <a-divider type="vertical" v-if="operationButtonsLength>1"/>
                </template>
                <template v-if="operationButtons.hasOwnProperty('edit')" v-show="isButtonVisible('edit',record)">
                    <a-button type="primary"
                              size="small"
                              :disabled="isButtonDisabled('edit',record)"
                              @click="handleOperate('edit',record)">
                        {{operationButtons.edit.hasOwnProperty('title')?operationButtons.edit.title:'编辑'}}
                    </a-button>
                    <a-divider type="vertical" v-if="operationButtonsLength>1"/>
                </template>
                <template v-if="operationButtons.hasOwnProperty('edit2')" v-show="isButtonVisible('edit2',record)">
                    <a-button type="primary"
                              size="small"
                              :disabled="isButtonDisabled('edit2',record)"
                              @click="handleOperate('edit2',record)">
                        {{operationButtons.edit2.hasOwnProperty('title')?operationButtons.edit2.title:'编辑'}}
                    </a-button>
                    <a-divider type="vertical" v-if="operationButtonsLength>1"/>
                </template>
                <template v-if="operationButtons.hasOwnProperty('audit')" v-show="isButtonVisible('audit',record)">
                    <a-button type="primary"
                              size="small"
                              :disabled="isButtonDisabled('audit',record)"
                              @click="handleOperate('audit',record)">
                        {{operationButtons.audit.hasOwnProperty('title')?operationButtons.audit.title:'审核'}}
                    </a-button>
                    <a-divider type="vertical" v-if="operationButtonsLength>1"/>
                </template>
                <template v-if="operationButtons.hasOwnProperty('delete')" v-show="isButtonVisible('delete',record)">
                    <a-popconfirm title="确认要删除吗?"
                                  @confirm="handleOperate('delete',record)"
                                  okText="是"
                                  cancelText="否">
                        <a-button type="danger"
                                  :disabled="isButtonDisabled('delete',record)"
                                  size="small">
                            {{operationButtons.delete.hasOwnProperty('title')?operationButtons.delete.title:'删除'}}
                        </a-button>
                    </a-popconfirm>
                </template>
            </div>
        </template>
    </a-table>
</template>

<script>
    import {getDate} from '@/libs/tools'

    export default {
        props: {
            showHeader: {
                required: false,
                type: [Boolean],
                default: () => {
                    return true
                }
            },
            columns: {
                required: true,
                type: Array,
                validator: value => {
                    if (value.length === 0) {
                        throw 'custom validator check failed for prop "columns" length error';
                    }

                    for (let i = 0; i < value.length; i++) {
                        if (value[i].hasOwnProperty('scopedSlots')
                            && value[i].scopedSlots.customRender === 'operation') {
                            if (!value[i].hasOwnProperty('buttons')) {
                                throw 'custom validator check failed for prop "columns" property buttons missing error';
                            }

                            if (value[i].buttons instanceof Object !== true) {
                                throw 'custom validator check failed for prop "columns" property buttons type error';
                            }
                        }
                    }
                    return true;
                }
            },
            rowSelection: {
                required: false,
                type: Object,
                default() {
                    return null;
                }
            },
            request: {
                required: true,
                type: Function,
            },
            requestInitFilter: {
                required: false,
                type: [Object],
                default: () => {
                    return {}
                }
            }
        },
        data() {
            return {
                loading: false,
                data: [],
                pagination: {},
                filter: {},//外部传入参数缓存
            }
        },
        computed: {
            size() {
                if (!this.pagination.hasOwnProperty('pageSize') || this.pagination.pageSize <= 20) {
                    return 'middle';
                } else {
                    return 'small';
                }
            },
            operationButtons() {
                let value = this.columns;
                for (let i = 0; i < value.length; i++) {
                    if (value[i].hasOwnProperty('scopedSlots')
                        && value[i].scopedSlots.customRender === 'operation') {
                        return value[i].buttons;
                    }
                }
                return {};
            },
            operationButtonsLength() {
                let value = this.columns;
                let buttons;
                for (let i = 0; i < value.length; i++) {
                    if (value[i].hasOwnProperty('scopedSlots')
                        && value[i].scopedSlots.customRender === 'operation') {
                        buttons = value[i].buttons;
                        break;
                    }
                }

                let count = 0;
                for (let key in buttons) {
                    count++;
                }
                return count;
            },
            columns_computed() {
                let temp = [];

                for (let i = 0; i < this.columns.length; i++) {
                    switch (this.columns[i].dataIndex) {
                        case 'party_info.joined_at':
                        case 'joined_at':
                        case 'opened_at':
                        case 'started_at':
                        case 'ended_at':
                        case 'leave_at':
                        case 'created_at':
                        case 'updated_at':
                        case 'finished_at':
                        case 'published_at':
                        case 'established_at':
                            this.columns[i].scopedSlots = {customRender: 'date_time'};
                            break;
                        case 'audit_status':
                        case 'audit_status_format':
                        case 'is_passed_format':
                            this.columns[i].scopedSlots = {customRender: 'audit_status'};
                            break;
                    }
                    temp.push(this.columns[i]);
                }
                return temp;
            }
        },
        methods: {
            getIndexNum(index) {
                return index + 1 + (this.pagination.current - 1) * this.pagination.pageSize
            },
            formatTimestamp(t) {
                return getDate(t);
            },
            getAuditReason(row) {
                if (row.hasOwnProperty('audit')) {
                    return row.audit.reason;
                }
                return null;
            },
            handleTableChange(pagination, filters, sorter) {
                const pager = {...this.pagination};
                pager.current = pagination.current;
                this.pagination = pager;
                this.fetch({
                    current_page: pagination.current,
                    filter: this.filter
                });
            },
            handleSearch(filter) {
                this.filter = filter;
                this.fetch({current_page: 1, filter});
            },
            fetch({current_page, filter}) {
                this.loading = true;
                if (!filter) {
                    this.filter = {};
                }
                return this.request({current_page, filter})
                    .then((res) => {
                        this.data = res.data.rows;
                        this.pagination = res.data.pagination;
                        this.loading = false;
                        return res;
                    })
                    .catch(res=>{
                        this.loading = false;
                    });
            },
            reFetch() {
                return this.fetch({current_page: this.pagination.current, filter: this.filter})
            },
            handleOperate(type, row) {
                let operate = this.$emit('handleOperate', type, row);
            },
            isButtonVisible(type, row) {
                switch (typeof this.operationButtons[type].visible) {
                    case "boolean":
                        return this.operationButtons[type].visible;
                    case "function":
                        return this.operationButtons[type].visible(row);
                    case "undefined":
                        return true;
                }
            },
            isButtonDisabled(type, row) {
                switch (typeof this.operationButtons[type].disable) {
                    case "boolean":
                        return this.operationButtons[type].disable;
                    case "function":
                        return this.operationButtons[type].disable(row);
                    case "undefined":
                        return false;
                }
            },
        },
        created() {
            this.filter = this.requestInitFilter;
            this.fetch({current_page: 1, filter: this.filter});
        },
    }
</script>
