<template>
    <a-spin :spinning="loading">
        <div class="container">
            <div class="columns" style="margin-top: 15px;">
                <div class="column col-6" v-for="row in data">
                    <div class="list box text-shadow">
                        <div class="list-item box">
                            <div class="img pull-left">
                                <a @click="handleOperate('check',row)">
                                    ×<img class="lazy" :src="getUrl(row.thumbnail_format)"
                                          style="display: inline;width: 200px;height: 128px">
                                </a>
                            </div>
                            <div class="block pull-right">
                                <p class="title">
                                    <a @click="handleOperate('check',row)">
                                        {{getShortString(row.post_title,40)}}
                                    </a>
                                    <template v-if="row.need_audit===1&&row.audit.status===2">
                                        <a-tooltip placement="top" :title="row.audit.reason">
                                            <a-tag color="#f50" style="font-size: 8px !important;">×</a-tag>
                                        </a-tooltip>
                                    </template>
                                </p>
                                <p class="desc">{{getShortString(row.post_excerpt,18)}}【详情】</p>
                                <p class="like">
                                    <span style="float: right;">
                                        <template v-if="operationButtons.hasOwnProperty('edit')"
                                                  v-show="isButtonVisible('edit',record)">
                                            <a @click="handleOperate('edit',row)"
                                               style="margin-right: 10px">
                                            <img :src="require('@/assets/pc/images/edit_ico.png')"
                                                 width="26" height="26"
                                            />
                                        </a>
                                        </template>
                                        <template v-if="operationButtons.hasOwnProperty('audit')"
                                                  v-show="isButtonVisible('audit',record)">
                                        <a @click="handleOperate('audit',row)"
                                           style="margin-right: 10px">
                                        <img :src="require('@/assets/pc/images/edit_ico.png')"
                                             width="26" height="26"
                                        />
                                        </a>
                                        </template>
                                        <template v-if="operationButtons.hasOwnProperty('delete')"
                                                  v-show="isButtonVisible('delete',record)">
                                             <a-popconfirm title="确认要删除吗?"
                                                           @confirm="handleOperate('delete',row)"
                                                           okText="是"
                                                           cancelText="否">
                                            <a>
                                                <img :src="require('@/assets/pc/images/delete_ico.png')"
                                                     width="26" height="26"
                                                />
                                            </a>
                                        </a-popconfirm>
                                        </template>
                                    </span>
                                </p>
                                <p>
                                    <i class="icon icon-people"></i> {{row.post_hits}} 人浏览
                                    <span class="date" style="float: right !important;padding-right: 20px">
                                    <i class="icon icon-time"></i> {{row.published_at_format}}
                                </span>
                                </p>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <a-pagination
                style="float: right"
                @change="handleChange"
                v-model="pagination.current"
                :pageSize="pagination.pageSize"
                :total="pagination.total"/>
    </a-spin>
</template>

<script>
    import {getDate, getImageUrl} from '@/libs/tools'

    export default {
        props: {
            columns: {
                // required: true,
                required: false,
                type: Array,
                validator: value => {
                    return true;
                    if (value.length === 0) {
                        throw 'custom validator check failed for prop "columns" length error';
                    }

                    for (let i = 0; i < value.length; i++) {
                        if (value[i].hasOwnProperty('scopedSlots')
                            && value[i].scopedSlots.customRender === 'operation') {
                            if (!value[i].hasOwnProperty('buttons')) {
                                throw 'custom validator check failed for prop "columns" property buttons missing error';
                            }

                            if (value[i].buttons instanceof Object !== true) {
                                throw 'custom validator check failed for prop "columns" property buttons type error';
                            }
                        }
                    }
                    return true;
                }
            },
            request: {
                required: true,
                type: Function,
            },
            requestInitFilter: {
                required: false,
                type: [Object],
                default: () => {
                    return {}
                }
            }
        },
        data() {
            return {
                loading: false,
                data: [],
                pagination: {
                    current: 0,
                    pageSize: 0,
                    total: 0,
                },
                filter: {},//外部传入参数缓存
            }
        },
        computed: {
            operationButtons() {
                let value = this.columns;
                for (let i = 0; i < value.length; i++) {
                    if (value[i].hasOwnProperty('scopedSlots')
                        && value[i].scopedSlots.customRender === 'operation') {
                        return value[i].buttons;
                    }
                }
                return {};
            },
        },
        methods: {
            getShortString(str, length) {
                if (str.length > length) {
                    return str.substring(0, length) + '...'
                } else {
                    return str;
                }
            },
            getUrl(url) {
                return getImageUrl(url);
            },
            handleChange(page, pageSize) {
                this.fetch({
                    current_page: page,
                    filter: this.filter
                });
            },
            fetch({current_page, filter}) {
                this.loading = true;
                if (!filter) {
                    this.filter = {};
                }
                return this.request({current_page, filter})
                    .then((res) => {
                        this.data = res.data.rows;
                        this.pagination = res.data.pagination;
                        this.loading = false;
                        return res;
                    })
                    .catch(res => {
                        this.loading = false;
                    });
                ;
            },
            reFetch() {
                return this.fetch({current_page: this.pagination.current, filter: this.filter})
            },
            handleOperate(type, row) {
                let operate = this.$emit('handleOperate', type, row);
            },
            isButtonVisible(type, row) {
                switch (typeof this.operationButtons[type].visible) {
                    case "boolean":
                        return this.operationButtons[type].visible;
                    case "function":
                        return this.operationButtons[type].visible(row);
                    case "undefined":
                        return true;
                }
            },
            isButtonDisabled(type, row) {
                switch (typeof this.operationButtons[type].disable) {
                    case "boolean":
                        return this.operationButtons[type].disable;
                    case "function":
                        return this.operationButtons[type].disable(row);
                    case "undefined":
                        return false;
                }
            },
        },
        created() {
            this.filter = this.requestInitFilter;
            this.fetch({current_page: 1, filter: this.filter});
        },
    }
</script>
