<template>
    <div>
        <a-card>
            <slot name="top"></slot>
            <table-component
                    ref="table"
                    :is="template"
                    :showHeader="showHeader"
                    @handleOperate="handleOperate"
                    :columns="columns"
                    :rowSelection="rowSelection"
                    :request="request"
                    :requestInitFilter="requestInitFilter"/>
        </a-card>
    </div>
</template>

<script>
    import TableComponent from './components/Table'
    import list_online_learning from './components/ListOnlineLearning'
    import list_online_examine from './components/ListOnlineExamine'
    import list_news from './components/ListNews'
    import article_comment from './components/ArticleComment'
    import notification from './components/Notification'

    export default {
        name: "Layout",
        components: {
            TableComponent, list_online_learning, list_online_examine, list_news, article_comment, notification
        },
        props: {
            showHeader: {
                required: false,
                type: [Boolean],
                default: () => {
                    return true
                }
            },
            columns: {//table的字段
                required: true,
                type: Array,
            },
            rowSelection: {
                required: false,
                type: Object,
                default() {
                    return null;
                }
            },
            request: {//ajax请求的方法
                required: true,
                type: Function,
            },
            requestInitFilter: {
                required: false,
                type: [Object],
                default: () => {
                    return {}
                }
            },
            template: {
                required: false,
                type: String,
                default() {
                    return 'TableComponent';
                }
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            reFetch() {
                //一般是删除或者新增操作之后重新获取数据 保持页码和筛选条件
                return this.$refs.table.reFetch()
            },
            handleOperate(type, row) {
                this.$emit('handleOperate', type, row);
            },
        }
    }
</script>

